<template>
 <div style="background-color: #002339">
   <div style=" line-height:60px;display:flex;">
     <div style="flex: 1; font-size: 20px">
       <span :class="collapseBtnClass" style="cursor: pointer;font-size: 20px"@click="collapse"></span>

       <el-breadcrumb separator="/" style="display: inline-block ;margin-left: 10px">
         <el-breadcrumb-item :to="item.path" v-for="item in paths">{{item.name}}</el-breadcrumb-item>
       </el-breadcrumb>

     </div>

     <div class="relative">
       <div class="padding">
         <span class="text">{{dateFormat(date)}}</span>
         <span class="time-temp">:</span>
         <span class="text">{{ dateFormat1(date) }}</span>
         <span class="time-ltemp">:</span>
         <span class="text">{{ dateFormat2(date) }}</span>
         <!--        <span>00</span>-->
         <!--        <span style="margin-right: 20px;margin-top: 1px">当前时间:{{dateFormat(date)}}</span>-->
       </div>
     </div>



     <el-dropdown style="width: 65px; cursor:pointer">
       <span>{{ user.username }}</span><i class="el-icon-arrow-down" style="margin-left: 5px"></i>
       <el-dropdown-menu slot="dropdown">
         <el-dropdown-item style="font-size: 14px">
           <span style="text-decoration: none" @click="logoout">退出</span>
         </el-dropdown-item>
       </el-dropdown-menu>
     </el-dropdown>
   </div>
 </div>
</template>

<script>
export default {
  name: "Header",
  props:{
    collapseBtnClass:String,
    collapse:''
  },
  data(){
    return{
      year:'',
      month:'',
      day:'',
      date:new Date(),
      paths:[],
      user:localStorage.getItem("user")?JSON.parse(localStorage.getItem("user")):{}
    }
  },
  mounted() {
    //显示当前日期时间
    let _this = this// 声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(() => {
      _this.date = new Date(); // 修改数据date
    }, 1000)
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  },

  created() {
    console.log(this.$router)
  },
  methods:{
    dateFormat(time) {
      var date=new Date(time);
      var year=date.getFullYear();
      /* 在日期格式中，月份是从0开始的，因此要加0
      * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
      * */
      var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
      // 拼接
      return hours;
    },
    dateFormat1(time1) {
      var date=new Date(time1);
      var year=date.getFullYear();
      /* 在日期格式中，月份是从0开始的，因此要加0
      * 使用三元表达式在小于10的前面加0，以达到格式统一  如 09:11:05
      * */
      var month= date.getMonth()+1<10 ? "0"+(date.getMonth()+1) : date.getMonth()+1;
      var day=date.getDate()<10 ? "0"+date.getDate() : date.getDate();
      var hours=date.getHours()<10 ? "0"+date.getHours() : date.getHours();
      var minutes=date.getMinutes()<10 ? "0"+date.getMinutes() : date.getMinutes();
      var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
      // 拼接
      return minutes;
    },
    dateFormat2(time2) {
      var date=new Date(time2);
      var seconds=date.getSeconds()<10 ? "0"+date.getSeconds() : date.getSeconds();
      // 拼接
      return seconds;
    },
    logoout(){
      this.$router.push("/login")
      localStorage.removeItem("user")
      this.$message.success("退出成功")
    }
  }
}
</script>

<style scoped>
span {
  color:white;
  font-size:15px;
}
.relative{
  /*max-width: 50px;*/
  margin-left: auto!important;
  margin-right: auto!important;
  position: relative;
  display: block;
  right: 30px;
}
.padding{
  position: relative;
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  bottom: 2px;
}
.time-temp{
  margin: 0 10px;
  position: relative;
  top:-3px;
}
.time-ltemp{
  margin: 0 10px;
  position: relative;
  top:-3px;

}
.text{
  /*background: linear-gradient(-210deg,#a2facf 0%,#64acff 100%);*/
  /*color: transparent;*/
  /*-webkit-background-clip: text;*/
  /*letter-spacing: -0.03em;*/
  /*display: block;*/
  /*align-items: center;*/
}
</style>